const fileMapToDirectoryMap = (fileMap) => {
    const directoryMap = { isDirectory: true, contents: {} };

    if (!fileMap) {
        return directoryMap;
    }

    for (let path in fileMap) {
        let parts = path.split('/');
        const fileName = parts[parts.length - 1];
        parts = parts.slice(0, parts.length - 1);

        let directory = directoryMap;
        for (let part of parts) {
            if (!directory.contents[part] || !directory.contents[part].isDirectory) {
                directory.contents[part] = { isDirectory: true, contents: {}, name: part };
            }
            directory = directory.contents[part];
        }
        directory.contents[fileName] = {...fileMap[path], name: fileName, path: path };
    }

    return directoryMap;
};

export default fileMapToDirectoryMap;
