import React from 'preact/compat';
import { useMemo, useState } from 'preact/hooks';

import SyntaxHighlighter from 'react-syntax-highlighter';
import highlighterStyle from './CodeStyleHljs';

import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import DownloadIcon from '@mui/icons-material/GetAppRounded';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import CheckIcon from '@mui/icons-material/Check';

import Table from '@mui/material/Table';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';

import CloseIcon from '../icons/CloseIcon';
import colors from '../colors';
import { clip } from '../../state/utils';

var FileSaver = require('file-saver');

const FileView = ({setCurrentFile, closeFile, currentFile, viewedFile, selectedFiles}) => {
    return (
    <Box
        sx={{
            width: '100%',
            height: '100%',
            boxSizing: 'border-box'
        }}
    >
        <FileHeader setCurrentFile={setCurrentFile} closeFile={closeFile} currentFile={currentFile}
            viewedFile={viewedFile} selectedFiles={selectedFiles} />
        <FileContent file={currentFile} />
    </Box>
    );
};

const FileHeader = ({setCurrentFile, closeFile, currentFile, viewedFile, selectedFiles}) => {
    const [scrollableLeft, setScrollableLeft] = useState(0);
    const [scrollableRight, setScrollableRight] = useState(1);

    const handleScroll = useMemo(() => (e) => {
        const leftDifference = e.target.scrollLeft;
        const rightDifference = e.target.scrollWidth - e.target.parentElement.scrollWidth - e.target.scrollLeft;
        setScrollableLeft(clip(leftDifference / 40, 0, 1));
        setScrollableRight(clip(rightDifference / 40, 0, 1));
    }, [setScrollableLeft, setScrollableRight]);

    return (
    <Box
        sx={{
            position: 'relative',
            paddingTop: '4px',
            height: '40px',
            overflow: 'hidden'
        }}
    >
        <Box
            onScroll={handleScroll}
            sx={{
                overflowX: 'auto'
             }}
        >
            <Stack direction="row" sx={{ width: 'fit-content'}} >
                { viewedFile &&
                    <FileHeaderItem setCurrentFile={setCurrentFile} closeFile={closeFile}
                        file={viewedFile} selected={viewedFile === currentFile} />
                }
                { selectedFiles && selectedFiles.map(file =>
                    <FileHeaderItem setCurrentFile={setCurrentFile} closeFile={closeFile}
                        file={file} selected={file === currentFile} key={file.path} />
                )}
            </Stack>
        </Box>
        <Box
            sx={{
                opacity: scrollableLeft,
                position: 'absolute',
                pointerEvents: 'none',
                left: 0,
                top: 0,
                height: '100%',
                width: '100px',
                background: `linear-gradient(to left, ${colors.featureSelectorBackground + '00'}, ${colors.featureSelectorBackground})`,
            }}
        />
        <Box
            sx={{
                opacity: scrollableRight,
                position: 'absolute',
                pointerEvents: 'none',
                right: 0,
                top: 0,
                height: '100%',
                width: '100px',
                background: `linear-gradient(to right, ${colors.featureSelectorBackground + '00'}, ${colors.featureSelectorBackground})`
            }}
        />
    </Box>
    );
};

const FileHeaderItem = ({setCurrentFile, closeFile, file, selected=false}) => {
    const handleCloseClick = (e) => {
        closeFile(file);
        e.stopPropagation();
    }

    return (
    <Button
        onClick={() => setCurrentFile(file) }
        sx={{
            height: '40px',
            whiteSpace: 'nowrap',
            padding: '2px 0 0 20px',
            color: colors.primaryText,
            backgroundColor: selected ? colors.codeBackground : colors.codeBackgroundInactive,
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            margin: '0 10px',
            borderRadius: '8px 8px 0 0',
            textTransform: 'none',
            fontSize: '14px',
            '& .MuiTypography-root': {
                overflow: 'hidden'
            },
            ':hover': {
                backgroundColor: colors.codeBackgroundHover
            }
        }}
    >
        <Typography sx={{ paddingTop: '3px' }}> {file.name} </Typography>
        <IconButton
            aria-label="close code view"
            onClick={handleCloseClick}
            sx={{
                padding: '4px',
                margin: '6px',
                '&:hover': {
                    backgroundColor: colors.featureSelectorBackground
                },
                '& .MuiSvgIcon-root': {
                    stroke: colors.primaryText,
                    fontSize: '14px'
                }
            }}
        >
            <CloseIcon />
        </IconButton>
    </Button>
    );
}

const FileContent = ({file}) => {
    let language;
    const [copiedStateValue, setCopiedState] = useState(false);
    const extension = file && file.name.slice(file.name.lastIndexOf(".") + 1).toLowerCase();
    switch (extension) {
        case "java":
        case "kotlin":
        case "gradle":
        case "properties":
        case "sql":
        case "yaml":
        case "xml":
        case "css":
        case "patch":
            language = extension;
            break;
        case "yml":
            language = "yaml";
            break;
        case "md":
            language = "markdown";
            break;
        case "html":
            language = "vbscriptHtml";
            break;
        default:
            language = "plaintext";
            break;
    }

    function handleFileDownload() {
        let newFile;
        if (typeof file.diff !== 'undefined') {
            let afterChanges = file.diff.map((line) => {
                if (!line.skip && !line.before) {
                    return line.content;
                }
                return 'undefined';
            }).filter((line) => typeof line !== 'undefined');
            newFile = new Blob([afterChanges.join('\n')]);
        } else {
            newFile = new Blob([file.content]);
        }
        FileSaver.saveAs(newFile, file.name);
    }

    async function handleFileCopy() {
        if (typeof file.diff !== 'undefined') {
            let afterChanges = file.diff.map((line) => {
                if (!line.skip && !line.before) {
                    return line.content;
                } 
                return 'undefined';
            }).filter((line) => typeof line !== 'undefined');
            await navigator.clipboard.writeText(afterChanges.join('\n'));
        } else {
            await navigator.clipboard.writeText(file.content);
        }
        setCopiedState(true);
        setTimeout(function() {
            setCopiedState(false);
        }, 3000);
    }

    return (
    <Box
        sx={{
            height: 'calc(100% - 44px)',
            overflow: 'auto'
        }}
    >
        <Box
            sx={{
                minWidth: '100%',
                minHeight: '100%',
                backgroundColor: colors.codeBackground,
                width: 'fit-content'
            }}
        >
            { file && file.content &&
                <SyntaxHighlighter
                    useInlineStyles="false"
                    language={language}
                    style={ highlighterStyle }
                    showLineNumbers={ true }
                >
                    { file.content }
                </SyntaxHighlighter>
            }

            { file && file.diff &&
                <DiffHighlighter file={ file } language={ language } highlighterStyle={ highlighterStyle } />
            }

            { file && !file.content && (file.isExecutable || file.isBinary) &&
                <Box
                    sx={{
                        width: '100%',
                        paddingTop: '20px',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        '& .MuiTypography-root': {
                            fontSize: '20px',
                            color: colors.secondaryText
                        }
                    }}
                >
                    {file.isExecutable &&
                        <>
                            <Typography>
                                This is an executable file
                            </Typography>
                            <Typography>
                                Download the project locally to run it
                            </Typography>
                        </>
                    }
                    {file.isBinary &&
                        <Typography>
                            This is a binary file
                        </Typography>
                    }
                </Box>
            }
        </Box>
        { file && file.content && 
        <IconButton
            onClick={handleFileDownload}
            sx= {{
                borderRadius: '4px',
                padding: '15px',
                position: 'absolute',
                bottom: 30,
                right: 25,
                width: 48,
                height: 48,
                backgroundColor: colors.action,
                '&:hover': {
                    backgroundColor: colors.actionHover
                },
                '&:disabled': {
                    backgroundColor: colors.actionDisabled
                },
                '& .MuiSvgIcon-root': {
                    color: colors.primaryText,
                    fontSize: '25px'
                }
            }}
        >
            <DownloadIcon />
        </IconButton>}
        { file && file.content && 
        <IconButton
            onClick={handleFileCopy}
            disabled={copiedStateValue}
            sx= {{
                borderRadius: '4px',
                padding: '15px',
                position: 'absolute',
                bottom: 30,
                right: 90,
                width: 48,
                height: 48,
                backgroundColor: colors.action,
                '&:hover': {
                    backgroundColor: colors.actionHover
                },
                '&:disabled': {
                    backgroundColor: colors.actionDisabled
                },
                '& .MuiSvgIcon-root': {
                    color: colors.primaryText,
                    fontSize: '25px'
                }
            }}
        >
            {copiedStateValue ? 
            <CheckIcon /> : 
            <ContentCopyIcon />
            }
        </IconButton>}
    </Box>
    );
}

const DiffHighlighter = ({ file, language, highlighterStyle }) => {
    return (<Table sx ={{
        backgroundColor: '#2e3949',
        '& .MuiTableCell-root': {
            fontSize: 'unset',
            padding: '0 0 0 1.5em',
            border: 'none',
            lineHeight: 1.1,
            '&.code-line-number': {
                textAlign: 'right',
                color: "#d8dee9",
                width: '2em',
                userSelect: 'none'
            },
            '&.code-line-symbol': {
                width: '1em',
                textAlign: 'right',
                userSelect: 'none'
            },
        },
        '& .MuiTableRow-root': {
            '&.skip': {
                backgroundColor: '#3c4659',
                '& .MuiTableCell-root': {
                    textAlign: 'left',
                    lineHeight: 2,
                    color: "#a1b5e1",
                    userSelect: 'none'
                }
            },
            '&.add .MuiTableCell-root': {
                backgroundColor: '#2e4939'
            },
            '&.remove .MuiTableCell-root': {
                backgroundColor: '#573c32'
            },
            '&.add .code-line-symbol': {
                color: "#98d268"
            },
            '&.remove .code-line-symbol': {
                color: "#d27868"
            }
        }
    }}>
        {file.diff && file.diff.map(line =>
            line.skip ?
            <TableRow className="skip">
                <TableCell colSpan={3}></TableCell>
                <TableCell><code>...</code></TableCell>
            </TableRow> :
            <TableRow className={line.before ? "remove" : line.after ? "add" : ""}>
                <TableCell className={"code-line-symbol"}> <code> {line.after ? "+" : line.before ? "-" : ""} </code> </TableCell>
                <TableCell className={"code-line-number"}> <code> {line.numberBefore} </code> </TableCell>
                <TableCell className={"code-line-number"}> <code> {line.numberAfter} </code> </TableCell>
                <TableCell>
                    <SyntaxHighlighter
                        useInlineStyles="false"
                        language={language}
                        style={ highlighterStyle }
                        showLineNumbers={ false }
                        customStyle = {{ padding: '1px', background: 'none' }}
                    >
                        { line.content }
                    </SyntaxHighlighter>
                </TableCell>
            </TableRow>
        )}
        
    </Table>
    );
}

export default FileView;