import React from 'preact/compat';
import { useState, useMemo } from 'preact/hooks';

import Box from '@mui/material/Box';

import DragDividerIcon from '../icons/DragDividerIcon';
import { clip } from '../../state/utils';

const dividerWidth = 6;

const DividedContainer = ({left, right}) => {
    const [dividerPos, setDividerPos] = useState(280);

    return <>
        <Box
            sx={{
                width: `${dividerPos}px`,
                overflow: 'hidden'
            }}
        >
            {left}
        </Box>
        <Divider dividerPos={dividerPos} setDividerPos={setDividerPos} />
        <Box
            sx={{
                width: `calc(100% - ${dividerPos + dividerWidth}px)`,
                overflow: 'hidden'
            }}
        >
            {right}
        </Box>
    </>
};

const Divider = ({dividerPos, setDividerPos}) => {
    const [currentDividerMove, setCurrentDividerMove] = useState(0);

    const handleMouseDown = useMemo(() => (e) => {
        const startMouseX = e.clientX;
        const target = e.target;
        let handleMouseUp, handleMouseMove;

        const getMove = (e) => {
            const width = target.parentElement.parentElement.scrollWidth;
            const move = clip(e.clientX - startMouseX, -dividerPos, width - dividerPos - dividerWidth);
            return move;
        }

        handleMouseUp = (e) => {
            document.removeEventListener('mouseup', handleMouseUp);
            document.removeEventListener('mousemove', handleMouseMove);
            setDividerPos(dividerPos + getMove(e));
            setCurrentDividerMove(0);
        };

        handleMouseMove = (e) => {
            setCurrentDividerMove(getMove(e));
        };

        document.addEventListener('mouseup', handleMouseUp);
        document.addEventListener('mousemove', handleMouseMove);
    }, [setCurrentDividerMove, setDividerPos, dividerPos]);

    return (
    <Box
        sx={{
            position: 'relative',
            width: `${dividerWidth}px`,
            height: '100%'
        }}
    >
        <Box
            onMouseDown={handleMouseDown}
            sx={{
                top: 0,
                left: `${currentDividerMove}px`,
                width: '100%',
                height: '100%',
                position: 'absolute',
                backgroundColor: '#4a5462',
                cursor: 'ew-resize',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-around',
                alignItems: 'center',
                '& .MuiSvgIcon-root': {
                    width: '6px',
                    height: '16px',
                    color: '#FFFFFF',
                    pointerEvents: 'none',
                    /* required */
                    transform: 'translate(0.2, 0)'
                }
            }}
        >
            <DragDividerIcon />
        </Box>
    </Box>
    );
};

export default DividedContainer;
