import React from 'preact/compat';
import { useMemo, useState } from 'preact/hooks';

import colors from '../colors';
import { LauncherPopup } from '../LauncherPopups';
import DirectoryView from './DirectoryView';
import FileView from './FileView';
import DividedContainer from './DividedContainer';
import ViewCommand from './ViewCommand';
import fileMapToDirectoryMap from './fileMapToDirectoryMap';
import PreviewIcon from '@mui/icons-material/FindInPageRounded';
import DiffIcon from '@mui/icons-material/SwapHorizRounded';

const LauncherPreview = ({ isDiff, isCommand, contents, clearContents }) => {
    const [viewedFile, setViewedFile] = useState(null);
    const [selectedFiles, setSelectedFiles] = useState([]);
    const [currentFile, setCurrentFile] = useState(null);

    const toggleOpen = () => {
        setViewedFile(null);
        setSelectedFiles([]);
        setCurrentFile(null);
        clearContents();
    };

    const directoryMap = useMemo(() => fileMapToDirectoryMap(contents), [contents]);

    const title = isDiff ? "Preview of differences between selection and baseline" : "Preview of project";
    const icon = isDiff ? DiffIcon : PreviewIcon;

    const selectFile = useMemo(() => (file) => {
        if (file === viewedFile) {
            setViewedFile(null);
        }
        setCurrentFile(file);
        setSelectedFiles(files => {
            if (files.indexOf(file) === -1) {
                return [file, ...files];
            }
            return files;
        });
    }, [setSelectedFiles, setCurrentFile, viewedFile]);

    const viewFile = useMemo(() => (file) => {
        setCurrentFile(file);
        if (selectedFiles.indexOf(file) === -1) {
            setViewedFile(file);
        }
    }, [setCurrentFile, setViewedFile, selectedFiles]);

    const closeFile = useMemo(() => (file) => {
        if (file === viewedFile) {
            setViewedFile(null);
            if (file === currentFile) {
                setCurrentFile(selectedFiles[0] || null);
            }
        } else {
            const index = selectedFiles.indexOf(file);
            setSelectedFiles(selectedFiles.filter((_, i) => i !== index));
            if (file === currentFile) {
                setCurrentFile(selectedFiles[index + 1] || selectedFiles[index - 1] || null);
            }
        }
    }, [setViewedFile, setSelectedFiles, setCurrentFile, viewedFile, selectedFiles, currentFile]);
    

    return ( 
        <>
            {isCommand
                ? <ViewCommand
                    contents={ contents }
                    open={ !!contents }
                    onClose={ toggleOpen }
                />
                : <LauncherPopup
                    title={title}
                    open={!!contents}
                    onClose={toggleOpen}
                    Icon={icon}
                    iconColor={colors.option}
                    fullHeight={true}
                    fullWidth={true}
                    sx={{
                        flexDirection: 'row',
                        padding: 0,
                        flexGrow: 1,
                        alignItems: 'stretch'
                    }}
                >
                    <DividedContainer
                        left={
                            <DirectoryView directory={directoryMap} currentFile={currentFile}
                                selectFile={selectFile} viewFile={viewFile} />
                        }
                        right={
                            <FileView setCurrentFile={setCurrentFile} closeFile={closeFile} currentFile={currentFile}
                                viewedFile={viewedFile} selectedFiles={selectedFiles} />
                        }
                    />
                </LauncherPopup>
            }
        </>
    );
};

export default LauncherPreview;
