import React from 'preact/compat';
import { useState, useMemo } from 'preact/hooks';

import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

import FolderClosedIcon from '@mui/icons-material/FolderRounded';
import FolderOpenIcon from '@mui/icons-material/FolderOpenRounded';
import FileIcon from '@mui/icons-material/InsertDriveFileRounded';
import DropDownIcon from '../icons/DropDownIcon';
import DropUpIcon from '../icons/DropUpIcon';

import colors from '../colors';

const getSortedFileNames = (files) => {
    if (!files) return [];

    let fileNames = Object.keys(files);
    return fileNames.sort((a, b) => {
        if (files[a].isDirectory !== files[b].isDirectory) {
            return files[b].isDirectory ? 1 : -1;
        }
        return a > b ? 1 : -1;
    });
}

const DirectoryView = ({directory, currentFile, viewFile, selectFile}) => {
    const files = directory?.contents;
    const fileNames = useMemo(() => getSortedFileNames(files), [files]);

    return (
    <Box
        sx={{
            width: '100%',
            height: '100%',
            boxSizing: 'border-box',
            flexShrink: 0,
            overflow: 'auto',
            color: colors.primaryText,
            fontWeight: 300,
            padding: '12px 2px 12px 12px',
            '& .MuiSvgIcon-root': {
                display: 'block',
                color: colors.primaryText,
                opacity: 0.5,
                fontSize: '14px'
            }
        }}
    >
        <Box
            sx={{
                minWidth: '100%',
                width: 'fit-content'
            }}
        >
            {files && fileNames.map(fileName =>
                <File file={files[fileName]} key={fileName}
                    currentFile={currentFile} viewFile={viewFile} selectFile={selectFile} />
            )}
        </Box>
    </Box>
    );
};

const Directory = ({directory, currentFile, viewFile, selectFile, openByDefault=false}) => {
    const files = directory.contents;
    const fileNames = useMemo(() => getSortedFileNames(files), [files]);

    const [open, setOpen] = useState(openByDefault);
    const toggleOpen = () => {
        setOpen(open => !open);
    };

    return (
    <Box
        sx={{
            transition: 'height 0.3s',
            maxHeight: open ? 'unset' : '20px',
            overflow: 'hidden'
        }}
    >
        <Button
            onClick={toggleOpen}
            sx={{
                display: 'flex',
                justifyContent: 'flex-start',
                flexDirection: 'row',
                alignItems: 'center',
                padding: '0 4px 0 0',
                color: 'inherit',
                textTransform: 'none',
                '& .MuiTypography-root': {
                    padding: '4px 5px 2px',
                    lineHeight: '14px',
                    whiteSpace: 'nowrap'
                },
                '&:hover': {
                    backgroundColor: colors.featureBackground + '40',
                    borderRadius: '4px'
                }
            }}
        >
            { open ? <DropDownIcon /> : <DropUpIcon /> }
            { open ? <FolderOpenIcon /> : <FolderClosedIcon /> }
            <Typography> {directory.name} </Typography>
        </Button>
        <Box
            sx={{
                marginLeft: '20px'
            }}
        >
            { open && fileNames.map(fileName =>
                <File file={files[fileName]} viewFile={viewFile} selectFile={selectFile} currentFile={currentFile}
                    key={fileName} openByDefault={fileNames.length <= 1} />
            )}
        </Box>
    </Box>
    )
};

const File = ({file, currentFile, viewFile, selectFile, openByDefault=false}) => {
    if (file.isDirectory) {
        return <Directory directory={file} openByDefault={openByDefault}
            viewFile={viewFile} selectFile={selectFile} currentFile={currentFile} />
    }

    const handleClick = (e) => {
        if (e.detail === 1) {
            viewFile(file);
        } else if (e.detail === 2) {
            selectFile(file);
        }
    }
    // const fileColor = file.diff ? '#cbffd8' : '#cdb38b'; 
    const ADD_COLOR = '#98d268', MODIFY_COLOR = '#f59e40';
    const fileColor = file.diff ? (file.isNew ? ADD_COLOR : MODIFY_COLOR) : 'inherit';
    const selectedFileBackground = file.diff ? (file.isNew ? ADD_COLOR : MODIFY_COLOR) : colors.featureBackground;

    const selected = currentFile === file;
    return (
    <Button
        onClick={ handleClick }
        className={selected ? "selected" : ""}
        sx={{
            color: fileColor,
            textTransform: 'none',
            display: 'flex',
            justifyContent: 'flex-start',
            flexDirection: 'row',
            alignItems: 'center',
            padding: '0 4px 0 14px',
            borderRadius: '4px',
            '& .MuiTypography-root': {
                whiteSpace: 'nowrap',
                padding: '4px 5px 2px',
                lineHeight: '14px',
            },
            '& .MuiSvgIcon-root': {
                color: fileColor
            },
            '&:hover': {
                backgroundColor: colors.featureBackground + '40',
            },
            '&.selected': {
                backgroundColor: selectedFileBackground,
                color: colors.primaryTextInverted,
                '& .MuiSvgIcon-root': {
                    color: colors.ternaryTextInverted,
                }
            }
        }}
    >
        <FileIcon/>
        <Typography> {file.name} </Typography>
    </Button>
    );
};

export default DirectoryView;
