import React from 'preact/compat';
import { useState } from 'preact/hooks';

import SyntaxHighlighter from 'react-syntax-highlighter';
import colors from '../colors';
import { LauncherPopup } from '../LauncherPopups';
import CodeIcon from '@mui/icons-material/Code';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import CheckIcon from '@mui/icons-material/Check';
import { Stack, Box, IconButton, Typography } from '@mui/material';

const ViewCommand = ({contents, open, onClose}) => {
    const [copiedStateValue, setCopiedState] = useState(false);

    async function handleCommandCopy() {
        await navigator.clipboard.writeText(contents.cli);
        setCopiedState(true);
        setTimeout(function() {
            setCopiedState(false);
        }, 3000);
    }

    return (
        <LauncherPopup
            title='Other ways to build this configuration'
            open={open}
            onClose={onClose}
            Icon={CodeIcon}
            iconColor={colors.option}
            sx={{
                flexDirection: 'row',
                padding: 0,
                flexGrow: 1,
                alignItems: 'stretch'
            }}
        >
            <Box
                sx={{
                    color: colors.primaryText,
                    padding: '10px 10px 20px'
                }}
            >
                <Typography sx={{ padding: '10px', fontSize: '20px' }}>Using the GDK CLI</Typography>
                <Stack
                    direction="row"
                    spacing="0"
                    sx={{
                        color: colors.primaryText,
                        borderRadius: '4px',
                        padding: '10px 20px 10px',
                        marginLeft: '10px',
                    }}
                >
                    <Box
                        sx={{
                            width: 'calc(100% - 100px)'
                        }}
                    >
                        <SyntaxHighlighter
                            style={{ 
                                'hljs': {
                                    "margin": 0,
                                    "display": "block",
                                    "background": colors.codeBackground,
                                    "color": '#d8dee9',
                                    "padding": "0.5em",
                                    "overflow-wrap": "break-word"
                                } 
                            }}
                            wrapLongLines
                        >
                            { contents && contents.cli }
                        </SyntaxHighlighter>
                    </Box>
                    <Box
                        sx={{
                            paddingLeft: '10px',
                            width: '60px'
                        }}
                    >
                        <IconButton
                            onClick={handleCommandCopy}
                            disabled={copiedStateValue}
                            sx= {{
                                borderRadius: '4px',
                                padding: '10px',
                                backgroundColor: colors.action,
                                width: 48,
                                height: 48,
                                '&:hover': {
                                    backgroundColor: colors.actionHover
                                },
                                '&:disabled': {
                                    backgroundColor: colors.actionDisabled
                                },
                                '& .MuiSvgIcon-root': {
                                    color: colors.primaryText,
                                    fontSize: '25px'
                                }
                            }}
                        >
                            {copiedStateValue ? 
                            <CheckIcon /> : 
                            <ContentCopyIcon />
                            }
                        </IconButton>
                    </Box>
                </Stack>
            </Box>
        </LauncherPopup>
    );
};

export default ViewCommand;